import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import SideBar from '../SideBar/SideBar';
import ItemCard from '../ItemCard/ItemCard';

import { getData } from '../fetcher/getData';
import { getCategory } from '../fetcher/getCategory';
// import Loader from '../Loader/Loader';

class Catalogue extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            status: false,
            items: '',
            genres: '',
            types: ''
        };
    }

    componentDidMount() {
        this.fillData();
        this.setState({ status: true });
    }

    fillData = () => {
        getData()
            .then(data => {
                this.setState({ types: data.types });
                this.getSwitchers(this.props.match.params.type, data);
            })
    }

    handleGenre = (e) => {
        getData()
            .then(data => {
                try {
                    switch (this.props.match.params.type) {
                        case 'books':
                            this.setState({
                                items: data.books.filter(item => item.genre === e.target.textContent),
                            });
                            break;
                        case 'movies':
                            this.setState({
                                items: data.movies.filter(item => item.genre === e.target.textContent),
                            });
                            break;
                        case 'music':
                            this.setState({
                                items: data.music.filter(item => item.genre === e.target.textContent),
                            });
                            break;
                        default:
                            break;
                    }
                } catch (error) {
                    console.log(error);
                }
            });
    }

    handleFilters = (title, priceOne, priceTwo) => {
        if (!title && !priceOne && !priceTwo) return;
        if (title && !priceOne && !priceTwo) {
            const searchTitle = this.state.items.filter(item =>
                item.title.toLowerCase().indexOf(title.toLowerCase()) >= 0);
            this.setState({
                items: searchTitle
            });
        }
        if (title && priceOne && !priceTwo) {
            const searchTitle = this.state.items.filter(item =>
                item.title.toLowerCase().indexOf(title.toLowerCase()) >= 0);
            const filterPO = searchTitle.filter(item =>
                item.price >= priceOne);
            this.setState({
                items: filterPO
            });
        }
        if (title && priceOne && priceTwo) {
            const searchTitle = this.state.items.filter(item =>
                item.title.toLowerCase().indexOf(title.toLowerCase()) >= 0);
            const filterPO = searchTitle.filter(item =>
                item.price >= priceOne);
            const filterPT = filterPO.filter(item =>
                item.price <= priceTwo);
            this.setState({
                items: filterPT
            });
        }
        if (!title && priceOne && priceTwo) {
            const filterPO = this.state.items.filter(item =>
                item.price >= priceOne);
            const filterPT = filterPO.filter(item =>
                item.price <= priceTwo);
            this.setState({
                items: filterPT
            });
        }
        if (!title && priceOne && !priceTwo) {
            const filterPO = this.state.items.filter(item =>
                item.price >= priceOne);
            this.setState({
                items: filterPO
            });
        }
        if (!title && !priceOne && priceTwo) {
            const filterPT = this.state.items.filter(item =>
                item.price <= priceTwo);
            this.setState({
                items: filterPT
            });
        }
    }

    handleType(type) { // = (e) =>
        // console.log(e);
        getData()
            .then(data => {
                this.getSwitchers(type, data); //e.target.textContent
            })
    }

    getSwitchers = (param, data) => {
        // console.log(param, data);
        try {
            switch (param) {
                case 'books':
                    this.setState({
                        items: data.books,
                        genres: data.genres_books
                    });
                    break;
                case 'movies':
                    this.setState({
                        items: data.movies,
                        genres: data.genres_movies
                    });
                    break;
                case 'music':
                    this.setState({
                        items: data.music,
                        genres: data.genres_music
                    });
                    break;
                default:
                    break;
            }
        } catch (error) {
            console.log(error);
        }
    }

    render() {
        const items = Array.from(this.state.items);
        const genres = Array.from(this.state.genres);
        const types = Array.from(this.state.types);
        return (
            <div className="d-flex flex-column">
                <div className="d-flex flex-row align-items-center">
                    <div className="d-flex flex-row">
                        {
                            types.map(type =>
                                <Link
                                    className="text-decoration-none p-4"
                                    to={`/${type}`}
                                    onClick={(e) => this.handleType(type)}
                                    key={type}
                                >
                                    {getCategory(type)}
                                </Link>
                            )
                        }
                    </div>
                    <div className="d-flex flex-row">
                        {
                            genres.map((genre) =>
                                <div class="btn btn-primary m-2" onClick={this.handleGenre}>{genre}</div>
                            )
                        }
                    </div>
                    <div name='reset' class="btn btn-primary" onClick={this.fillData}>Сбросить все фильтры</div>
                </div>
                <SideBar handleFilters={this.handleFilters} fillData={this.fillData} />
                <div class="row row-cols-1 row-cols-md-2">
                    {
                        !this.state.status
                            ? 'Loading...'
                            : items.map((item) =>
                                <Link className="text-decoration-none"
                                    to={`/${this.props.match.params.type}/product=${item.id}`}
                                >
                                    <ItemCard key={item.id} {...item} />
                                </Link>
                            )
                    }
                </div>
            </div>
        );
    }
}

export default Catalogue;