import React from 'react';

const getImg = (item) => {
    if (!item.img_thumb) {
        return <img class="img-thumbnail" src={item.img} alt='cover image' />;
    } else {
        let newSrc = 'https:' + item.img_thumb;
        return <img class="img-thumbnail" src={newSrc} alt='cover image' />;
    }
}

const ItemCard = (item) => {
    return (
        <div class="col mb-4 d-flex justify-content-center">
            <div className="card w-75">
                <div className="d-flex justify-content-center">
                    {getImg(item)}
                </div>
                <div class="card-body">
                    <div class="card-title">
                        <h4>{item.title}</h4>
                    </div>
                    <div>{item.price} ₽</div>
                </div>
            </div>
        </div>
    );
};

export default ItemCard;